.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.text-danger{
  color: #CE1931 !important;
}

.text-success{
  color: #1ECC89 !important;
}

.bg-danger{
  background: #E01B3C !important;
}

.bg-success{
  background-color: #1ECC89 !important;
}

.btn-outline-success{
  border-color: #1ECC89 !important;
  color: #1ECC89 !important;
}

.btn-outline-success:hover{
  background: none;
}

.btn-outline-danger{
  border-color: #E01B3C;
  color: #E01B3C !important;
}

.btn-outline-danger:hover{
  background: none;
}